










































































































































































































.ItemBox {
  width: 95%;
  margin: 0 auto;
  background-color: white;
  position: relative;
  .noEdit {
    pointer-events: none;
  }
  .mySubscribe {
    position: absolute;
    top: 1vh;
    right: 2vw;
    background: #15cfa400;
    border-radius: 0.5vw;
    border: 1px solid #003f30;
    padding: 0.5vh 2vw;
    font-size: 4vw;
    color: #008466;
    line-height: 4vw;
  }
  .appHomeTitBox {
    height: 6.55vw;
    line-height: 6.55vw;
    margin: 2vw 0 0 2vw;
    font-size: 4vw;
    padding-top: 1vw;
    display: flex;
    justify-content: space-between;
    .titleName {
      font-size: 5vw;
      font-weight: bold;
    }
    .appointment {
      width: 20vw;
      text-align: center;
      border-radius: 0.3vw;
      margin-right: 1vw;
      color: #ffffff;
      background-color: #20cfa4;
    }
  }
  .appHomeContent {
    width: calc(100% - 2vw);
    margin: 2.6vw 0 0 0;
    padding: 0 1vw;
    display: flex;
    flex-wrap: wrap;
    .contentItem {
      width: 100%;
      display: flex;
      margin-bottom: 2.42vw;
      position: relative;
      .itemLabel {
        width: 22;
        margin-right: 0.9vw;
        padding-left: 1vw;
        font-size: 3.8vw;
        font-weight: bold;
        color: #008c8a;
        // text-align-last: justify;
      }
      .redSpot {
        position: absolute;
        top: 2vw;
        font-size: 15vw;
        border-radius: 100%;
        // background-color: red;
        width: 1.2vw;
        height: 1.2vw;
      }
      .itemValue {
        flex: 1;
        background: #fff;
        font-size: 3.2vw;
        font-family: PingFangSC, PingFangSC-Regular;
        font-weight: 400;
        text-align: left;
        color: #868786;
        padding-left: 1vw;
        input {
          width: 100%;
          border: none;
          padding: 0.5vw 0;
          background: #fff;
        }
        input::placeholder {
          font-size: 2.66vw;
        }
        textarea {
          width: 100%;
          height: 100%;
          background: #fff;
          border: none;
          resize: none;
        }
        select {
          width: 100%;
          background: #fff;
          color: #868786;
          border: none;
        }
      }
      .w77 {
        height: 4.4vw;
        line-height: 4.4vw;
        max-width: 77vw;
      }
      .W0 {
        max-width: 77vw;
        height: 20vw;
      }
    }
  }
  .wrap {
    .contentItem {
      width: 50%;
    }
  }
  .Btn {
    text-align: center;
    height: 6vh;
    line-height: 6vh;
    margin: 6vw 0 0 3vw;
    width: 90vw;
    font-size: 4vw;
    // border: #15cfa4 0.4vw solid;
    background-color: #15cfa4;
    color: white;
  }
}
