



































































.ItemBox {
  width: 95%;
  margin: 0 auto;
  background-color: white;
  position: relative;
  .mySubscribe {
    position: absolute;
    top: 1vh;
    right: 2vw;
    background: #15cfa4;
    border-radius: 0.5vw;
    padding: 0.5vh 2vw;
    font-size: 4vw;
    color: #fff;
    line-height: 4vw;
  }
  .BomTitBox {
    margin: 0vw 0 0 1vw;
    font-size: 4vw;
    padding-top: 1vw;
    box-sizing: border-box;
    .titleName {
      width: 100%;
      font-size: 4.4vw;
      font-weight: bold;
    }
    p {
      padding: 2vw;
      font-size: 3.5vw;
      color: #868786;
      background-color: white;
    }
  }
  .z {
    .titleName {
      font-size: 4vw;
      z-index: 10;
    }
    .convention {
      width: 100%;
      color: #868786;
      li {
        margin-left: 7vw;
      }
    }
    font-size: 3vw;
    color: #008c8a;
  }
  .MandatoryBox {
    display: flex;
    margin: 3vw 0 0 0;
    font-size: 3.6vw;
    align-content: center;
    input {
      margin: 0.6vw 3vw 0 2vw;
    }
    div {
      color: #5a9aff;
      border-bottom: 0.5vw #5a9aff solid;
    }
  }
  .Btn {
    text-align: center;
    height: 6vh;
    line-height: 6vh;
    margin: 6vw 0 0 3vw;
    width: 90vw;
    font-size: 4vw;
    border: #15cfa4 0.4vw solid;
    background-color: #15cfa4;
    color: white;
  }
}
